import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { UserMoveinoutTrackerComponent } from './user-moveinout-tracker.component';
import { UserMoveinComponent } from './components/user-movein/user-movein.component';
import { UserMoveoutComponent } from './components/user-moveout/user-moveout.component';

const routes: Routes = [
	{ path: '', redirectTo: 'movein', pathMatch: 'full'},
	{ path: 'movein', component: UserMoveinComponent },
	{ path: 'moveout', component: UserMoveoutComponent },
	{ path: '**', redirectTo: 'movein', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserMoveinoutTrackerRoutingModule { }
